import { LocalStorageService } from './../core/service/local-storage.service';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Hubspot } from 'hotspotCustomEvents';
import { UserSession } from 'core/sessions';
import { environment } from 'environments/environment';
import { Constants } from 'core/constants';


declare var AOS: any;
declare var $: any;
declare var Swiper: any;
declare var Intercom:any;
declare var CryptoJS:any;



@Component({
  selector: 'app-landingpage',
  templateUrl: './landingpage.component.html',
  styleUrls: ['./landingpage.component.css']
})
export class LandingpageComponent implements OnInit {
public gymName=UserSession.getDynamicElements(Constants)
  constructor(private router: Router,
    private _activatedRoute: ActivatedRoute,
  ) { }

  ngAfterViewInit() {
    Hubspot.homePageView();

    
	var swiper = new Swiper('.swiper-container.slidersOne', {
		autoplay: {
    	delay: 3000,
  				},
		slidesPerView: 3,
      spaceBetween: 10,
      slidesPerGroup: 1,
	  loop: true,      
	  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  breakpoints: {
	  1024: {
          slidesPerView: 3,
          spaceBetween: 10,
        },
	  600: {
          slidesPerView: 3,
          spaceBetween: 10,
        },
		490: {
          slidesPerView: 2,
          spaceBetween: 10,
        },
	  320: {
          slidesPerView: 1,
          spaceBetween: 20,
        },
        
  }
    });
	
	var slidersTwo = new Swiper('.swiper-container.slidersTwo', {
		autoplay: {
    	delay: 3000,
  		},
	    loop: true,      
	    });


  }
  ngOnInit() {
    setTimeout(() => {
      try{
        AOS.init({
          duration: 1200,
        })
      }catch(e){
        console.log(e)
      } 
    }, 2000);
    
   
    if (LocalStorageService.getIsLoggedIn() == true) {
      this.redirectToUserPage();
    }
    else {
      this._activatedRoute.params.subscribe(params => {
        if (params['isLink']) {
          window['$']('#LoginButton').click();
        }
      });
    }
  }
  redirectToUserPage() {
    this.router.navigate(['/classes']);

  }

  redirect(){
    this.router.navigate(['/classes'], { queryParams: { type: 'priceModel', 'id': 'VIRTUAL'} });
  }

  initializeAndOpenIntercom(){
    if (UserSession.isLoggedIn()) {
    var hash = CryptoJS.HmacSHA256(LocalStorageService.getUser().data.email, "N6GEqX94QBXoeYJKgh8RjfL6qjsTCDaHGL9BFg0t");   
    var hashHex = hash.toString(CryptoJS.enc.Hex);
    var user = LocalStorageService.getUser().data;
   Intercom("boot", {
      app_id: environment.intercom_app_id,
     name: LocalStorageService.getUser().data.firstName+" "+LocalStorageService.getUser().data.lastName, // Full name
      email: LocalStorageService.getUser().data.email, // Email address
      user_hash:hashHex,
      created_at:new Date().toLocaleDateString() , // Signup date
      plan_name: "basic", // Strings
      avatar: {
        "type": "avatar", 
        "image_url" :user.profileImageThumbnail
      }
    });
    Intercom('show');
  }
  else{
    Intercom("boot", {
      app_id: "qp091xcl",
      name : "Guest "+Math.floor(100000 + Math.random() * 900000)

    });
    Intercom('show');

  }
  }


}


